function _classCallCheck(instance,Constructor){if(!(instance instanceof Constructor)){throw new TypeError("Cannot call a class as a function");}}function _possibleConstructorReturn(self,call){if(!self){throw new ReferenceError("this hasn't been initialised - super() hasn't been called");}return call&&(typeof call==="object"||typeof call==="function")?call:self;}function _inherits(subClass,superClass){if(typeof superClass!=="function"&&superClass!==null){throw new TypeError("Super expression must either be null or a function, not "+typeof superClass);}subClass.prototype=Object.create(superClass&&superClass.prototype,{constructor:{value:subClass,enumerable:false,writable:true,configurable:true}});if(superClass)Object.setPrototypeOf?Object.setPrototypeOf(subClass,superClass):subClass.__proto__=superClass;}var ConstructionOfMarisDungeon=function(_Component){_inherits(ConstructionOfMarisDungeon,_Component);function ConstructionOfMarisDungeon(){var _ref;var _temp,_this,_ret;_classCallCheck(this,ConstructionOfMarisDungeon);for(var _len=arguments.length,args=Array(_len),_key=0;_key<_len;_key++){args[_key]=arguments[_key];}return _ret=(_temp=(_this=_possibleConstructorReturn(this,(_ref=ConstructionOfMarisDungeon.__proto__||Object.getPrototypeOf(ConstructionOfMarisDungeon)).call.apply(_ref,[this].concat(args))),_this),_this.render=function(){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_1__Shared_Page_styles__["a" /* Page */],null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_4__Shared_Header_Header__["a" /* default */],{currentScroll:1}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__Shared_BlogPost_BlogPost__["a" /* default */],null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'Construction of Mari\u2019s Dungeon',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'It\'s the late 90s. Kouhei Kadano has failed to get another light novel published. The publishers praise his writing but they still all tell him the same thing, \u201CNo one will buy this!\u201D    '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'With this mantra fixed in his mind and a heart filled with both jealousy and appreciation of what publishers consider popular and marketable, Kadano creates the Boogiepop and Others series. Boogiepop, as described by Kadano himself at the end of one of his novels, is not just the novel itself but also an ideology which represents the middle ground between \u201Cboogie\u201D sensibilities, describing personal, polarizing, and highly niche works and \u201Cpop\u201D works which are generally designed to have mass-appeal and be easily accessible works.    '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'In this sense, our game uses this as a key tenant, and is designed around the \u201Cboogiepop\u201D sensibilities of trying to take one\'s own personal preferences and tastes and add in the right elements that will make them more accessible without losing what makes it special to you in the first place.    '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'Dungeon Philosophy',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'As an extension of this idea, each dungeon in Rainfall is meant to be the capstone of a character\u2019s arc. They are designed to be a manifestation of the character\u2019s view of the world and takes the form of a distortion of Iriya. While not an exact one-to-one corruption of the city, certain characteristics should influence the setting of the stage, its color palette, and its denizens.     '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'As an example, in the Tutorial Dungeon, we tried to realize this through portraying a city that had been consumed by nature and long abandoned. It was meant to be heavily isolated with the citizens of Iriya being present but turned to stone to give the feeling of a city filled with people and at the same time not. The palate was dominated by whites, grays, and greens and the stage itself was designed to be entirely linear. This area is meant to represent the distorted viewpoint of Jin\u2019s initial feelings, those of self-isolation and being around people but never connecting nor speaking with them.    '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'Mari\u2019s Dungeon Conception',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'In constructing Mari\'s Dungeon, it was important for us to begin with a strong foundation. For this purpose we decided to create a foundation that was built on contrast. Blending together disparate concepts that would act as foils for one another and strengthen the foundation as a whole. One of the inspirations for this idea came from how the early 2000\'s anime Samurai Champloo constructed its setting.     '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Samurai Champloo was part of the Spring 2004 simulcast in Japan and featured an anachronistic blend of Edo Era Samurai and modern pulp sensibilities including hip-hop and bohemian cultural aesthetics. In a similar way to how director Quentin Tarantino is known for blending different aesthetics, like in the revenge samurai movie Kill Bill or his take on the Spaghetti Western in Django Unchained, Samurai Champloo aims to mix both of its inspirations together and have them serve as foils to each other. The absurdity of some of the filler episodes of Champloo, like the introduction of zombies or having samurai compete in a baseball tournament, formed the defining features of the show and exhibited the same Tarantino and boogiepop sensibilities that Kadano sought and that we too want to present our own spin on.    '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Specifically for Mari, we wanted her dungeon to exist in pure contrast to the Tutorial Dungeon.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Initially we thought of nonlinearity. The first goal then was to design a completely non-linear dungeon that was held together by a set of scenes that would guide the player to the various key parts of the area. When presented with nonlinearity we were drawn towards the idea of a spin on Disneyland but incorporating a gambling aesthetic into it where you\u2019d be taking Mari on a date throughout the park. However, a pure theme park felt too disparate from the idea of a \u201Cdistorted city\u201D and lacked some of the boogiepop feelings we wanted to emulate.    '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'As a result, we decided to take on a remix of this \u201CGambling Theme Park\u201D with one of our Last Hymn Areas the Cherry Blossom Park.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_9__images_cherryblossom_png___default.a,alt:'Cherry Blossom Park'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'This Cherry Blossom Park was an area inside of Last Hymn that was a huge, feudal-style park merged with a graveyard in which many ghosts roamed about. It featured what we called the \u201CEdo Favela\u201D which was a stack of wooden buildings with Chinese hip and gable roofs iconic to that era, and was used for breaking up the paths and giving it the aesthetic of a lived in area. With this as our base we started on an upgraded version of this area merged with a more modern Gambling tone.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'A modern Vegas setting merged with Edo shrines and architecture allows for both aesthetics to blend together and enhance each other. While the idea of an Edo era village may be considered \u201CToo Eastern\u201D or \u201CToo Obscure\u201D, by blending in the elements of the city, the park, and the neon lights we can present a unique location while having a familiar locale.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'Environmental Storytelling',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Another technique we tried to use inside Mari\u2019s dungeon was working on our environmental storytelling. While there are quite a few definitions of what this means, to us if this area is supposed to be a theme park then the different key areas for scenes and boss encounters are our rides. However, to keep the park interesting we need to have the connecting areas between these rides be visually interesting and distinct as landmarks as well.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_10__images_sidearea_png___default.a,alt:'Side Area'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'What we chose to go with was creating sub areas that are all uniquely themed and intersect the paths between the three casinos that serve as the goal of the area. Pictured above is an example of one of these areas, a festival area with different food and game stalls. In the final version of this dungeon these will all be filled with various items but for now serves as a landmark for players and visually distinct from the other parts of the area.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'Dungeon Design Process',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Whenever we design areas, we always start out with the \u201Cblocking\u201D of the map. This usually includes taking a couple of pieces, in this case a standard rectangular wooden building and a group of Cherry Trees, and carving out the map. This is because even without any detail or other objects, the map should still be fun to move around. While detailing in the map can certainly hide level design flaws, the end result will always be less satisfying than a map that was fun to interact with when completely blank.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_5__images_block_png___default.a,alt:'Blocking Part 1'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'During this phase some of the \u201Ckey areas\u201D will also be filled in like the save points, the dungeon entrance, and entrances to other sub-areas. Again this is purely to mimic what movement around the dungeon will be like.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'After the initial blocking, the next step is to add in various wall details like doors or windows and create additional building layouts beyond the basic rectangle. This variance is key in establishing the aesthetic of the area while also helping to ensure the area is memorable for the player. For 2D maps in specific, layering is one of the easiest way to add depth to a map and give it a sense of scale. In the sequence of images below you can see how the progression in adding in different sections of the map that are below, above, and at the same level as the player are necessary for creating an interesting looking area.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_6__images_block2_png___default.a,alt:'Blocking Part 2'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'After the initial blocking I filled in some of the wall details and added in all of the house shapes instead of just using the same rectangular block.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_7__images_block3_png___default.a,alt:'Blocking Part 3'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Then I wanted to cover up the ground texture as much as possible so we took the idea of the stone path from the Cherry Blossom Park and applied it here. It also outlines some other possible paths that the NPCs around the map will take into the other buildings that are inaccessible to the player.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_8__images_block4_png___default.a,alt:'Blocking Part 4'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Next we added in the overhead elements, mainly accomplished through the strings of lanterns that will fill the map and be lit up. These also serve to divide up the blank space between the different blocks without interfering with player movement. From here we can continue filling in with further doodads like the NPCs on the side, trash cans, etc. Once we have the base layers setup as shown above the doodads will \u201Ccomplete\u201D the area and provide different color backdrops to main red, pinks, and browns of the map.')));},_temp),_possibleConstructorReturn(_this,_ret);}return ConstructionOfMarisDungeon;}(__WEBPACK_IMPORTED_MODULE_0_react__["Component"]);/* harmony default export */ __webpack_exports__["a"] = (ConstructionOfMarisDungeon);

