function _classCallCheck(instance,Constructor){if(!(instance instanceof Constructor)){throw new TypeError("Cannot call a class as a function");}}function _possibleConstructorReturn(self,call){if(!self){throw new ReferenceError("this hasn't been initialised - super() hasn't been called");}return call&&(typeof call==="object"||typeof call==="function")?call:self;}function _inherits(subClass,superClass){if(typeof superClass!=="function"&&superClass!==null){throw new TypeError("Super expression must either be null or a function, not "+typeof superClass);}subClass.prototype=Object.create(superClass&&superClass.prototype,{constructor:{value:subClass,enumerable:false,writable:true,configurable:true}});if(superClass)Object.setPrototypeOf?Object.setPrototypeOf(subClass,superClass):subClass.__proto__=superClass;}var LessonsLearnedFromClassicWow=function(_Component){_inherits(LessonsLearnedFromClassicWow,_Component);function LessonsLearnedFromClassicWow(){var _ref;var _temp,_this,_ret;_classCallCheck(this,LessonsLearnedFromClassicWow);for(var _len=arguments.length,args=Array(_len),_key=0;_key<_len;_key++){args[_key]=arguments[_key];}return _ret=(_temp=(_this=_possibleConstructorReturn(this,(_ref=LessonsLearnedFromClassicWow.__proto__||Object.getPrototypeOf(LessonsLearnedFromClassicWow)).call.apply(_ref,[this].concat(args))),_this),_this.render=function(){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_1__Shared_Page_styles__["a" /* Page */],null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_4__Shared_Header_Header__["a" /* default */],{currentScroll:1}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__Shared_BlogPost_BlogPost__["a" /* default */],null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'3 Lessons Learned From Classic WoW',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'1. Convenience can be detrimental',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'As humans we seek out anything that will make our lives easier. Convenience is a gift that allows us to work less and do more. However, achieving convenience comes at a cost, to do something easier or with less work conversely means we no longer have to work or hard or struggle to achieve what we want. But it\'s in adversity that we make meaningful memories and grow as people. This double-edged sword needs to be balanced otherwise you\u2019ll end up in a situation where you no longer need to do anything to succeed or on  the opposite side where despite what you do you will not succeed.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'In terms of World of Warcraft, we\u2019ve gradually made our way to the side of too much convenience. Currently you can sit in the same city all day and end up inside of raids, dungeons, gain reputation, level up your professions, do side activities and games, and nearly every other facet of the game without ever seeing the world. While no system on its own is the cause of this nor necessarily detrimental its the combination of these things that has displaced the sense of achievement in the game.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'A good example of this is doing dungeons. In Classic WoW, if I want to run Shadowfang Keep for the first time as an Alliance character I have to do the following assuming I have never been outside of the Human Area starting zone:'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('ul',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Find 4 other players on your server wanting to run the dungeon'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone flies to Stormwind'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone takes the Deeprun Tram to Ironforge'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone leaves Ironforge to Dun Morough'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone leaves Dun Morough to Loch Modan'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone leaves Loch Modan to Wetlands'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone leaves Wetlands to Arathi Highlands'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone leaves Arathi Highlands to Hillsbrad Foothills'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Everyone leaves Hillsbrad Foothills to Silverpine Forest'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('li',null,'Head to Shadowfang Keep to the west')),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'By making this journey, I will have passed through 7 zones, unlocked 5 flight paths, and as a level 20-ish character will have been through zones ranging from levels 1 to 40. I will have ventured through enemy territory, especially in Hillsbrad Foothills and Silverpine Forest where I will have to fight the opposite faction just to make it to the dungeon. In the future, this journey will be less tedious with the different flightpaths unlocked but I feel like I accomplished something on my character and have earned the right to enter the dungeon. But more importantly along the way to the dungeon I have had to overcome other challenges with my party members not related to the dungeon such as having to fight those enemy players or going through higher level zones.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Some dungeons even feature a pre-dungeon section. This is an area leading from the map to the dungeon often filled with elites of similar enemies you\u2019ll find in the dungeon with some quests outside that also lead into the dungeon itself. Wailing Caverns has a massive cavern section, Blackfathom Deeps has an underground temple you need to travel through, and Blackrock Depths has a jumping puzzle you need to complete.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_5__images_sfk_jpg___default.a,alt:'Shadowfang Keep'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Modern WoW has replaced this with a Looking For Group (LFG) tool that will teleport you and 4 random people to the inside of the dungeon. I will have missed out on picking up these crucial flightpaths for the future and have missed out on the player created storyline of getting to this area. However, in its place it has added in way more enemy mechanics. Bosses in Classic WoW tend to have only one or two main abilities and a single phase to go through. This holds true up until the end of the first raid tier as well. Modern WoW though especially in the last few expansions with the introduction of Mythic and Mythic+ has added in raid like encounters with multiphase boss fights and challenging mob packs into the dungeons themselves. This is an amazing change to the game and should be one of the main selling point of modern WoW unfortunately...'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'2. There must always be the opportunity for new rewards',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'I ran into this same problem with one of my first large-scale games Last Hymn. While we had an interesting combat system and a cool world to explore, players universally avoided and dreaded entering combat since it only depleted their health and offered no rewards. Instead it took away from the main selling point of the game which was its atmosphere and environments.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Humans tend to act when they have something to gain or when they have something to lose. There is an opportunity cost to do anything and this is something everyone considers before taking action. So if a developer puts a quest in a game it needs to provide either a physical reward like gear, experience, money, items, etc. or a immaterial reward like opening up a new dungeon, questline, or having another player indebted to you. There are many rewards that something can give you but everything needs to have one. When I run The Deadmines for the first time, I have quests inside to grant me experience, gear upgrades to receive, and the experience of completing the dungeon for the first time. However, after the first run on my first character why else would I run the dungeon besides experience and the potential for gear? Eventually this too fades away and you\u2019ll never go back here unless it is on an alternate character.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'This holds true for questing zones as well. If a quest is for levels 20-25, if you are level 28 there is no reason to do the quest anymore if it only offers experience or gear and once you outlevel a zone there is no reason to ever return.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_6__images_chicken_jpg___default.a,alt:'Chicken in Westfall'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'While in modern WoW a levelling zone may only be useful from 78-80, in Classic WoW many zones often covered a wide range of levels. Take Stranglethorn Vale for instance it offers quests from levels 30-50, has a major neutral town, and serves as a central port for both Horde and Alliance, and later on even had a raid present in the zone. Stranglethorn Vale was useful as a zone for over half the game. Higher level dungeons also shared this quality where a level 30-40 dungeon like Maraudon contained useful gear and items for max level players encouraging them to revisit older content. Many quests also encourage revisiting older areas too such as a level 20ish quest in the Redridge Mountains requiring players to farm Hops off of mobs in Westfall a lower level zone so they can obtain a brew to bring back to Redridge. Many quests in Classic WoW force a player to go across multiple zones for different items, deliveries, or mobs making it so you will always see other people in whatever zone you are in.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'This is one of the travesties of modern WoW. A zone like Felwood for instance is completely abandoned the moment you outlevel the content. There is no reason to ever go back there nor are there any quests that take you through that area again. This creates a lonely experience and an essentially useless zone. This is why I was ecstatic to see the Class Order Hall campaigns in Legion where each class went through many of the old world zones and raids to do quests. Shamans would lead a crusade back through the Firelands raid from multiple expansions ago, Priests would explore hidden areas underneath the Tirisfal Glades, and more. This was amazing content even if many classes shared the retreaded zones. It made the world feel more alive and lived in as well as made older zones relevant again.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h2',null,__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('strong',null,'3. Complexity should come from breadth not depth of choices',__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('br',null))),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'recent trend in games has been on creating \u201Cemergent gameplay\u201D which is where a bunch of simple mechanics combine together to form complex situations. Games like Breath of the Wild for instance offers the player a bunch of mechanics to use multiple types of bombs, time stop, magnesis, and more. While each move is very one-dimensional on its own, combining them together or being put in different scenearios force you to use the same move in many different ways. A breadth of tools over a breadth of situations creates the best environment for player-created scenarios to occur and for some of the most memorable experiences to happen.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'In Classic WoW for instance, many of my most memorable moments so far have not been part of the prescripted content created by Blizzard but instead moments that I and the other players made happen.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'It\u2019s moments like the run from Southshore to Scarlet Monastery where you have to fight through an army of enemy players to even enter the dungeon or dueling another player but debuffing them before the fight or getting a bunch of low levelled characters together to take on enemies 8+ levels more than you creating your own raid like encounter.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_3__Shared_BlogPost_BlogPost_styles__["d" /* BlogImage */],{src:__WEBPACK_IMPORTED_MODULE_7__images_rematch_jpg___default.a,alt:'Duel Outside Stormwind'}),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'The game provides the freedom to allow these things to happen and is more flexible in how players can complete the content than other games. This permeates from quest and dungeon design down to each individual ability. Take the mage class design for instance, excluding the balance of the class compared to others, a Frost Mage\u2019s main rotation is purely just Frost Bolt then Frost Bolt and then Frost Bolt again and you repeat this until the enemy dies. This is obviously not thrilling combat design and I would agree that the main rotation of all classes in Classic WoW are as boring as this. This is one thing I much prefer in more recent expansions like Legion. However, where the classes feel truly interesting is in their situational moves.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Even though I only have a 1 button main rotation, I have several dozen situational abilities that I need to weave in constantly otherwise I\u2019ll die while levelling. When I\u2019m out of mana I have Evocate on a long cooldown to recover all my mana while standing still for a bit, my mana gems to recover a little mana instantly, and my wand so I can start regening mana during combat. All three fit similar purposes and during a series of combat encounters I\u2019ll most likely use all of them and have to choose when one makes the most sense over the other. The depth of these individual abilities is so low but the combination of low depth abilities leads to complex decisions and fun choices.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Next imagine a scenario where my character is near death and I need to play defensively. Well I have Ice Block that prevents all damage but I can\u2019t attack anymore, Frost Nova that roots enemies, Cone of Cold to heavily slow enemies, Blink to dash forward, and some more reactive abilities like Counterspell to cancel an enemies spellcast and Cold Snap to reset all of my frost abilities. So a quick combo that Frost Mages learn is to slow/root enemies and then blink away so you can have time to cast spells at enemies. The combo of Cone of Cold and Frost Nova means you can kite enemies for quite a while, especially when you use Cold Snap to repeat those two crowd control moves again. Having this way to crowd control enemies while also being very squishy means you have to balance distance between yourself and the enemy. You have to go into melee range to crowd control groups but that also means you will get hit by one or two stray melee strikes unless you use your one mobility move. Again all of these abilities are very basic but a player will have to combo these moves in very different manners depending on the scenario and what abilities are up at the time. So even though my main dps moveset is one move, in actual combat I can easily use several dozen abilities.')));},_temp),_possibleConstructorReturn(_this,_ret);}return LessonsLearnedFromClassicWow;}(__WEBPACK_IMPORTED_MODULE_0_react__["Component"]);/* harmony default export */ __webpack_exports__["a"] = (LessonsLearnedFromClassicWow);

